import { baseRequest, normalRequest } from './base';

//api of self(user) management of self user and is different than the userManagement APIs

const loginUser = async (data, signal) => {
  try {
    return await baseRequest("api/v1/user/auth", "POST", data, signal);
  } catch (e) {
    alert(e);
  }
};

