import isEmpty from "./isEmpty"
import IsEmpty from "../App/components/Reusables/IsEmpty";

function isEmailValid(email) {
    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

const validateSendCode = (email) => {


    const errors = {}

    if (isEmpty(email)) {
        errors.email = 'Provide your email.'
    }

    if (!isEmpty(email) && !isEmailValid(email)) {

        errors.email = 'Provide a valid email.'
    }

    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}



const validateResetPassword = (data) => {

    const errors = {}

    if (isEmpty(data.password)) {
        errors.password = 'Provide the password.'
    }

    if (!isEmpty(data.password) && data.password !== data.password2) {

        errors.password2 = 'Passwords do no match.'
    }

    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}

const validateCodeInput = (code) => {
    const errors = {}

    if (isEmpty(code)) {
        errors.code = 'Enter the code.'
    }

    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}

export { validateSendCode, validateResetPassword, validateCodeInput, isEmailValid }